import Calendar from 'tui-calendar';
import 'tui-calendar/dist/tui-calendar.css';
import 'tui-date-picker/dist/tui-date-picker.css';
import 'tui-time-picker/dist/tui-time-picker.css';

const calendar = new Calendar('#calendar', {
  defaultView: 'month',
  useCreationPopup: true,
  useDetailPopup: true,
  // taskView: false,
  month: {
    daynames: [
      '',
      '',
      '',
      '',
      '',
      '',
      ''
    ],
    startDayOfWeek: 0
  },
  week: {
    daynames: [
      '',
      '',
      '',
      '',
      '',
      '',
      ''
    ],
    startDayOfWeek: 0
  },
  template: {
    popupDetailLocation(schedule) {
      return `Location : ${schedule.location}`;
    }
  }
});

const App = {
  plusFun() {
    const groupname = $('#groupname').val();

    $.ajax({
      url: '/dataSave', //     URL 
      data: {id: 'jen',
        groupName: groupname}, // HTTP     
      type: 'POST', // HTTP  (GET, POST)
      dataType: 'json' //    
    });
    const plusUl = document.createElement('li');
    plusUl.innerHTML = `<a style=" display: flex; justify-content: space-between;"><p>${groupname}</p><img src = "./public/assets/img/ 7.png"/></a>`;
    document.getElementById('a').appendChild(plusUl);
  },
  changeMonth() {
    calendar.changeView('month', true);
  },
  changeDay() {
    calendar.changeView('day', true);
  },
  changeWeek() {
    calendar.changeView('week', true);
  },
  changeCalendar() {
    calendar.id[0].render();
  },
  clear() {
    calendar.clear();
  //   calendar.createSchedules(schedules, true);
    // calendar.render();
  },
  changePrev() {
    calendar.prev();
  },
  changeNext() {
    calendar.next();
  },
  changeHide() {
    calendar.render();
  }
};

window.App = App;

calendar.render();

$.ajax({
  url: '/', //     URL 
  data: {id: 'jen'},
  type: 'POST', // HTTP  (GET, POST)
  dataType: 'json', //    
  success(json) {
    let tables = [];

    for (let i = 0; i < json.length; i = i + 1) {
      tables.push({
        id: i + 1,
        name: json[i].gr_name,
        color: '#ffffff',
        bgColor: '#9e5fff',
        dragBgColor: '#9e5fff',
        borderColor: '#9e5fff'
      });
    }

    calendar.setCalendars(tables);
  }
})

$.ajax({
  url: '/initial', //     URL 
  data: {id: 'jen'},
  type: 'GET', // HTTP  (GET, POST)
  dataType: 'json', //    
  success(json) {
    calendar.createSchedules(json);
  }
})

//  
calendar.on('beforeCreateSchedule', scheduleData => {
  const schedule = {
    calendarId: 'Major Lecture',
    id: String(Math.random() * 100000000000000000),
    title: scheduleData.title,
    isAllDay: scheduleData.isAllDay,
    start: scheduleData.start,
    end: scheduleData.end,
    category: scheduleData.isAllDay ? 'allday' : 'time'
  };

  const tempDate = schedule.start;
  const tempDate2 = schedule.end;

  $.ajax({
    url: '/insert',
    data: {
      calendarId: 'Major Lecture',
      id: String(Math.random() * 100000000000000000),
      title: scheduleData.title,
      isAllDay: scheduleData.isAllDay,
      start: `${tempDate.getFullYear()}-${tempDate.getMonth() + 1}-${tempDate.getDate()} ${tempDate.getHours()}:${tempDate.getMinutes()}:${tempDate.getSeconds()}`,
      end: `${tempDate2.getFullYear()}-${tempDate2.getMonth() + 1}-${tempDate2.getDate()} ${tempDate2.getHours()}:${tempDate2.getMinutes()}:${tempDate2.getSeconds()}`,
      category: scheduleData.isAllDay ? 'allday' : 'time'
    },
    type: 'POST',
    dataType: 'json',
    success() {
      calendar.createSchedules([schedule]);
      alert('  ');
    }
  });
});

//  
calendar.on('beforeUpdateSchedule', scheduleData => {
  const {schedule} = scheduleData;

  const tempDate = schedule.start;
  const tempDate2 = schedule.end;

  $.ajax({
    url: '/update', //     URL 
    data: {
      calendarId: schedule.calendarId,
      id: schedule.id,
      title: schedule.title,
      start: `${tempDate.getFullYear()}-${tempDate.getMonth() + 1}-${tempDate.getDate()} ${tempDate.getHours()}:${tempDate.getMinutes()}:${tempDate.getSeconds()}`,
      end: `${tempDate2.getFullYear()}-${tempDate2.getMonth() + 1}-${tempDate2.getDate()} ${tempDate2.getHours()}:${tempDate2.getMinutes()}:${tempDate2.getSeconds()}`
    },
    type: 'POST', // HTTP  (GET, POST)
    dataType: 'json', //    
    success() {
      calendar.updateSchedule(schedule.id, schedule.calendarId, schedule);
    }
  });
});

//  
calendar.on('beforeDeleteSchedule', scheduleData => {
  const {schedule} = scheduleData;

  $.ajax({
    url: '/delete',
    data: {
      id: schedule.id
    },
    type: 'POST',
    dataType: 'json',
    success() {
      calendar.deleteSchedule(schedule.id, schedule.calendarId);
    }
  });
});
